/**
 * 메인 이미지 경로는 frontmatter에 입력하기 때문에 resources 폴더에 따로 둔다.
 * 외부 이미지가 아니라면 require를 사용해서 이미지를 직접 가져오는 방법을 사용한다.
 *
 * resources 폴더 내부의 이미지 파일들은 gatsby-source-filesystem 플러그인으로
 * File node가 생성된 상태여야 한다.
 *
 * https://www.gatsbyjs.org/packages/gatsby-source-filesystem/?=file#gatsby-source-filesystem
 *
 */
export default function getMainImageURL(url) {
  const isRemoteImage = /^https?:\/\//.test(url)

  return isRemoteImage ? url : require(`../resources/${url}`)
}



