// @flow
import React from 'react'
import Helmet from 'react-helmet'
import { PageWrapper, PostContentWrapper } from '../components/content-wrapper'
import NavBar from '../components/nav-bar'
import Footer from '../components/footer'
import PostList from '../components/post-list'
import ReactPaginate from 'react-paginate'
import styled, { css } from 'styled-components'
import { navigateTo } from 'gatsby-link'
import { centeredX } from '../styles/mixins'
import { rhythm } from '../utils/typography'

const currentPageStyle = css`
  font-weight: bold;
  background-color: #76b835;
  & a {
    color: #fff;
  }
`

const PaginationContainer = styled.div`
  margin: ${rhythm(1)} auto;
  position: relative;
  display: block;
  height: 3rem;

  & > ul {
    ${centeredX()};
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    list-style-type: none;
    padding: 0;
    margin: 0;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  }

  & > ul > li {
    margin: 0;
    font-size: 0.8rem;
    background-color: #fff;

    &:hover {
      cursor: pointer;
      /* ${currentPageStyle}; */
    }

    &.active {
      ${currentPageStyle};
    }

    &.previous,
    &.next {
    }

    & a {
      display: block;
      padding: 0.7rem;
      outline: none;
    }

    &:not(:first-child) {
      border-left: 1px solid #f1f1f1;
    }
  }
`

/**
 * props.pathContext에 현재 페이지의 정보가 전달된다.
 *
 * @class Index
 * @extends {React.Component}
 */
class PageIndex extends React.Component {
  handlePageClick = data => {
    let { selected } = data
    selected = selected === 0 ? '' : selected + 1

    navigateTo(`/${selected}`)
  }

  render() {
    const { siteMetadata } = this.props.data.site
    const { pathContext } = this.props

    return (
      <PageWrapper>
        <Helmet
          title={siteMetadata.title}
          meta={[
            { name: 'description', content: `${siteMetadata.description}` },
            { property: 'og:type', content: 'article' },
            { property: 'og:url', content: `${siteMetadata.url}` },
            { property: 'og:title', content: `${siteMetadata.title}` },
            {
              property: 'og:description',
              content: `${siteMetadata.description}`,
            },
          ]}
          link={[{ rel: 'canonical', href: `${siteMetadata.url}` }]}
        />
        <NavBar />
        <PostContentWrapper>
          <PostList posts={this.props.pathContext.group} />

          <PaginationContainer>
            <ReactPaginate
              previousLabel={<i className="fa fa-angle-left" />}
              nextLabel={<i className="fa fa-angle-right" />}
              breakLabel={
                <a href="">
                  <i className="fa fa-ellipsis-h" />
                </a>
              }
              forcePage={pathContext.index - 1}
              breakClassName={'break-me'}
              pageCount={pathContext.pageCount}
              marginPagesDisplayed={1}
              pageRangeDisplayed={5}
              onPageChange={this.handlePageClick}
              containerClassName={'pagination'}
              subContainerClassName={'pages pagination'}
              activeClassName={'active'}
            />
          </PaginationContainer>
        </PostContentWrapper>
        <Footer />
      </PageWrapper>
    )
  }
}

export default PageIndex

export const pageQuery = graphql`
  query PaginatedIndexQuery {
    site {
      siteMetadata {
        title
        author
        description
        githubUrl
        url
      }
    }
  }
`



